    },
    created() {
      this.searchContent.activitySearchVal = this.$route.query.id && this.$route.query.id !== 'undefined' ? parseInt(this.$route.query.id) : ''
      // 三方平台店内分类
      this.getCategoryId()
    },
    mounted() {
      this.doSearchHandel(true)
    },
    components: {
      "excel-add": AddByExcel,
      "choose-store": ChooseStore,
      "region": Region
    },
    methods: {
      RegionChange(params) {
        this.searchContent.regionCode = params.regionCode
        this.doSearchHandel(true)
      },
      /**
       * [getCategoryId 三方平台店内分类]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(requestUrl.getCategoryTree, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(三方平台店内分类):", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandels 搜索]
       * @param  {[type]} _params [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        // 将选中数据重置为初始值
        this.checkedList = []
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }

        this.$http.post(requestUrl.listPage, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data) {
              if(res.data.result && res.data.result.length) {
                this.tableData = res.data.result
              }else {
                this.tableData = []
              }
              this.total = res.data.total
            }else {
              this.total = 0
              this.tableData = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })

      },
      /**
       * [deleteHandel 删除]
       * @param  {[type]} data [description]
       * @return {[type]}      [true  单个删除    false   批量删除]
       */
      deleteHandel(type, data) {
        let params = {
          DIFFCITY: true
        }
        let idAndActivityTypeArr = []
        if(type) {
          let item = {
            id: data.id,
            activityType: data.activityType
          }
          idAndActivityTypeArr.push(item)
          params.idAndActivityTypeList = idAndActivityTypeArr
        }else {
          idAndActivityTypeArr = this.checkedList.map(item => {
            return {
              id: item.id,
              activityType: item.activityType
            }
          })
          params.idAndActivityTypeList = idAndActivityTypeArr
        }
        this.$confirm('您确定要删除所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(requestUrl.delUrl, {...params}).then(res => {
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.id, 'id', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('id', row.id, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.id] ? '' : hash[next.id] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.id] ? '' : hash[next.id] = true && item.push(next)
            return item
          }, [])
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'id')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [importStore 引入门店]
       * @return {[type]} [description]
       */
      importStore() {
        if(this.checkedList.length === 0) {
          this.$message("请选择商品")
          return
        }
        let activityIdList = this.checkedList.map(item => {
          return item.activityId
        })
        let activityIds = Array.from(new Set(activityIdList))
        if(activityIds.length > 1) {
          this.$notify.warning({
            title: '温馨提示',
            message: '请选择同一个活动的商品'
          })
          return
        }
        let _param = {
          title: '批量引入门店',
          platformId: this.searchContent.platformId,
          activityType: 51,
          regionCode: this.searchContent.regionCode
        }
        this.$refs.chooseStore.setDefault(_param)
      },
      /**
       * [activityStatusFormatter 格式化状态]
       * @return {[type]} [description]
       */
       activityStatusFormatter(row, column, cellValue, index) {
        if(cellValue === 0) {
          return "未发布"
        }else if(cellValue == 1) {
          return "已发布"
        }else if(cellValue == 2) {
          return "未开始[发布中]"
        }else if(cellValue == 3) {
          return "进行中[发布中]"
        }else if(cellValue == 4) {
          if(row.activityType === 11) {
            return "已结束[发布中]"
          }else {
            return "已结束"
          }
        }else if(cellValue == 5) {
          return "已终止"
        }else if(cellValue == 6) {
          return "已失效"
        }
      },
      /**
       * [onFocusHandel 输入]
       * @return {[type]} [description]
       */
      onFocusHandel(index, filedName) {
        setTimeout(() => {
          this.tableData[index][`${filedName}Temp`] = this.tableData[index][filedName]
          this.showUpdate = true
          this.currentIndex = index
          this.currentFiledName = filedName
        }, 400)
      },
      onBlurHandel() {
        setTimeout(() => {
          let index = this.currentIndex
          let filedName = this.currentFiledName
          this.tableData[index][filedName] = this.tableData[index][`${filedName}Temp`]
          this.showUpdate = false
          this.currentIndex = -1
          this.currentFiledName = ''
        }, 400)
      },
      sureUpdateHandel(row) {
        let index = this.currentIndex
        let filedName = this.currentFiledName
        if(filedName === 'price') {
          // 原价
          if(!salePriceReg.test(row[filedName]) || parseFloat(row[filedName]) === 0) {
            this.$message.warning('原价是大于0，小数保留小数点后2位') 
            return
          }else {
            if(parseFloat(row[filedName]) < parseFloat(row['preSalePrice'])) {
              this.$message.warning('原价必须大于等于预售价') 
              return
            }
          }
        }else if(filedName === 'preSalePrice') {
          // 活动价
          if(!salePriceReg.test(row[filedName]) || parseFloat(row[filedName]) === 0) {
            this.$message.warning('预售价是大于0，小数保留小数点后2位') 
            return
          }else {
            if(parseFloat(row[filedName]) > parseFloat(row['price'])) {
              this.$message.warning('原价必须大于等于预售价') 
              return
            }
          }
        }else if(filedName === 'stock') {
          // 门店库存
          if(row[filedName] && row[filedName] != 0) {
            if(!numReg.test(row[filedName])) {
              this.$message.warning('门店限售库存为大于等于0的正整数') 
              return
            }
          }
        }else if(filedName === 'platformCurStock') {
          // 区域库存
          if(row[filedName] && row[filedName] != 0) {
            if(!numReg.test(row[filedName])) {
              this.$message.warning('区域限售库存为大于等于0的正整数') 
              return
            }
          }
        }else if(filedName === 'limitCount') {
          // 每期限购
          if(row[filedName] && row[filedName] != 0) {
            if(!numReg.test(row[filedName])) {
              this.$message.warning('每人每期限购为大于等于0的正整数') 
              return
            }
          }
        }else if(filedName === 'sortNum') {
          // 排序
          if(!numReg.test(row[filedName])) {
            this.$message.warning('排序为大于等于0的正整数') 
            return
          }
        }
        let rowMsg = {...row}
        let params = {
          DIFFCITY: true,
          idAndActivityTypeList: [{
            id: rowMsg.id,
            activityType: rowMsg.activityType
          }],
          updateGoodsData: rowMsg
        }
        utils.showLoading()
        this.$http.post(requestUrl.updateUrl, {...params}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            this.$message.success("更新成功")
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        }) 
      },
      /**
       * @description: 确认选择门店
       * @param {type} 
       * @return: 
       */
      confirmHandel(val) {
        let storeNos = val.checkedList.map(item => {
          return item.storeNo
        })
        let idsList = this.checkedList.map(item => {
          return item.id
        })
        let activityId = this.checkedList[0].activityId
        let activityType = val.activityType || ''
        this.$http.post(requestUrl.addGoodsToStore, {ids: idsList, storeNos: storeNos, activityType: activityType, activityId: activityId, DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            this.$refs.chooseStore.closeDialog()
            this.$refs.chooseStore.closeDisabled()
            this.$message.success("引入门店成功")
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$refs.chooseStore.closeDisabled()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        }) 
      },
      mouseEnterImg(index, row) {
        if(row.activityStatus === 4 || row.activityStatus === 5 || this.imageUploadLoading === true) {
          return
        }
        this.showChangImg = true
        this.currentChangeImgIndex = index
        this.currentRowMsg = {...row}
      },
      mouseLeaveImg() {
        this.showChangImg = false
        this.currentChangeImgIndex = -1
      },
      imageUploadReg (file) {
        const isPic = file.type.indexOf('image/') !== -1
        const isJPG = file.type === 'image/jpeg' || 'image/png'
        const isSizeNum = file.size <= 1 * 300 * 1024
        if (!isPic || !isJPG || !isSizeNum) {
          this.$message.error('商品图片建议上传大小不超过300K的710*420像素的jpg或png图片')
        }
        return isPic && isJPG && isSizeNum
      },
      /**
       * [getToken 通过后台封装的接口获取七牛的token]
       * @return {[type]}            [description]
       */
       getToken () {
        return this.$http.post(fileUrlList.getQiNiuToken, {}).then(res => {
          if (res.code == 0) {
            let token = res.data.token
            let fileName = res.data.key
            return {fileName, token}
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.imageUploadLoading = false
          this.$notify.error({
            title: '获取七牛token提示',
            message: e.message
          })
        })
      },
      /**
       * [emitUpload 上传图片]
       * @param  {[type]} e [description]
       * @return {[type]}   [description]
       */
      emitUpload(e) {
        this.imageUploadLoading = true
        utils.showLoading()
        this.getToken().then((res) => {
          // 开始上传图片
          let uploader = qiniu.upload(e.file, res.fileName, res.token)
          uploader.subscribe({
            error: () => {
              this.imageUploadLoading = false
              utils.clearLoading()
            },
            complete: (res) => {
              // 添加内容设置图片地址
              let urlImage = 'http://retailimg01.benlailife.com/' + res.key
              this.imageUploadLoading = false
              utils.clearLoading()
              // 设置对应的图片地址
              this.currentRowMsg.preSaleImageUrl = urlImage
              this.sureUpdateHandel(this.currentRowMsg)
            },
            next: res => {
            }
          })
        })
      },
      platformCurStockHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover">
            <div>
              <p>区域限售库存，即所有门店共享的总预售库存；</p>
              <p>门店限售即每个门店的预售库存，可在门店预售商品中调整门店库存；</p>
              <p>每个商品至少要设置一种限售库存方式，最大可设置9999；</p>
              <p>如果既设了区域限售，又设了门店限售，则先校验区域库存，再校验门店库存；</p>
            </div>
            <span slot="reference">区域限售库存<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      /**
       * @description: 
       * @param {type} 
       * @return: 
       */ 
      exportData() {
        this.exportLoading = true
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.$http.post(requestUrl.listPage, { ...this.searchContent }).then(res => {
          this.exportLoading = false
          if(res.code === 0) {
            let tempData = res.data && res.data.result && res.data.result.length ? res.data.result : []
            tempData.forEach(item => {
              item.cutoffTypeStr = `${item.cutoffTypeName || ''} ${item.cutoffTime || ''}`
              if(item.activityStatus === 0) {
                item.activityStatusStr = "未发布"
              }else if(item.activityStatus == 1) {
                item.activityStatusStr = "已发布"
              }else if(item.activityStatus == 2) {
                item.activityStatusStr = "未开始[发布中]"
              }else if(item.activityStatus == 3) {
                item.activityStatusStr = "进行中[发布中]"
              }else if(item.activityStatus == 4) {
                if(item.activityType === 11) {
                  item.activityStatusStr = "已结束[发布中]"
                }else {
                  item.activityStatusStr = "已结束"
                }
              }else if(item.activityStatus == 5) {
                item.activityStatusStr = "已终止"
              }else if(item.activityStatus == 6) {
                item.activityStatusStr = "已失效"
              }
            })
            const tHeader = utils.objectToArray(this.exportConfig, false)
            const filterVal = utils.objectToArray(this.exportConfig, true)
            exportSheet.ExportJsonToExcel({
              header: tHeader,
              filterVal,
              data: tempData,
              filename: `预售商品管理商品`
            })
            setTimeout(() => {
              this.$notify.success({
                title: '温馨提示',
                message: `预售商品管理商品`
              })
              this.isDisabled = false
            }, 500)
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.exportLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      showDownLoadDialog() {
        if(this.$refs.excelAdd) {
          let _val = {
            title: '批量修改预售商品',
            tempConfig: this.templateConfig
          }
          this.$refs.excelAdd.setDefaultMsg(_val)
        }
      },
      doConfirmHandel(value) {
        let excelDataList = value.dataList || []
        this.$http.post(requestUrl.checkPreGoodsUpdate, [...excelDataList]).then(res => {
          this.$refs.excelAdd.closeDisabled()
          this.$refs.excelAdd.closeDialog()
          if(res.code === 0) {
            this.hasErrorMsgData = res.data && res.data.length ? res.data : []
            let isOk = this.hasErrorMsgData.find(item => {
              return item.msgType === 1
            })
            if(isOk) {
              this.showTipErrorMsg = true
            }else {
              // 商品验证通过  调用批量更新接口
              this.doUpdateExportGoodsGoods(res.data)
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$refs.excelAdd.closeDisabled()
          this.$refs.excelAdd.closeDialog()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      doUpdateExportGoodsGoods(goodsList) {
        this.$http.post(requestUrl.updateExportGoods, [...goodsList]).then(res => {
          if(res.code === 0) {
            this.$message.success("批量修改成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      downLoadErrorMsg() {
        let downloadExcelConfig = {...this.templateConfig}
        downloadExcelConfig.msgTypeStr = '验证结果'
        downloadExcelConfig.msg = '失败原因'
        this.hasErrorMsgData.forEach(item => {
          item.msgTypeStr = item.msgType === 1 ? '验证失败' : '验证成功'
        })
        const tHeader = utils.objectToArray(downloadExcelConfig, false)
        const filterVal = utils.objectToArray(downloadExcelConfig, true)
        exportSheet.ExportJsonToExcel({
          header: tHeader,
          filterVal,
          data: this.hasErrorMsgData,
          filename: `批量修改预售商品`
        })
        setTimeout(() => {
          this.$notify.success({
            title: '温馨提示',
            message: `批量修改预售商品`
          })
          this.isDisabled = false
        }, 500)
      }
    }
  }
</script>


